/* webkit slider=========================== */



.slider1 {
  /* position: absolute; */
  -webkit-appearance: none;
  min-width: 100%;
  border-radius: 5px;
  background: #ffffff;
  outline: none;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
  float: left;
  height: 2px;

  /* top: 16px; */
  /* margin-top: 16px; */
  /* margin-right: 29%; */


}

.slider1:hover {
  opacity: 1;
  cursor: pointer;
}


.slider1::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2vh;
  height: 2vh;
  border-radius: 50%;
  background: #259494;
  cursor: pointer;
}
#myRange1::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #feffff;
  cursor: pointer;
}

.slider1::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}



